import { request } from 'utils';

const prefix = '/api/categories';

export const getGraduateClassification = () =>
  request({
    prefix,
    url: `/GraduateClassification`,
    method: 'GET',
  });

export const addGraduateClassification = dataAddNew =>
  request({
    prefix,
    url: `/GraduateClassification`,
    data: dataAddNew,
    method: 'POST',
  });

export const getGraduateClassificationById = id =>
  request({
    prefix,
    url: `/GraduateClassification/GetById`,
    params: { id },
    method: 'GET',
  });

export const editGraduateClassification = graduateClassification =>
  request({
    prefix,
    url: `/GraduateClassification`,
    data: graduateClassification,
    method: 'PUT',
  });

export const deleteGraduateClassification = id =>
  request({
    prefix,
    url: `/GraduateClassification`,
    data: { id },
    method: 'DELETE',
  });



