import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Row, Card, Form, Input } from 'antd';
import {
  addGraduateClassification,
  getGraduateClassificationById,
  editGraduateClassification,
} from 'services/GraduateClassification';
import { getQueryParamId } from 'utils/urlUtil';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    graded: '',
    englishName: '',
    fromScores: '',
    toScores: '',
    fromScoresTen: '',
    toScoresTen: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormRatingGraduate extends Component {
  state = {
    teacherId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    if (getQueryParamId()) {
      this.setState({ teacherId: getQueryParamId() });
      this.getTeacherById(getQueryParamId());
    }
  };

  getTeacherById = async code => {
    const teacherMap = await getGraduateClassificationById(code);
    const initialValue = handleInitialValue(teacherMap.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    const { teacherId } = this.state;
    this.props.form.validateFields(async (err, values) => {
      if (teacherId) {
        await editGraduateClassification(values);
        this.props.history.goBack();
      } else {
        await addGraduateClassification(values);
        this.props.history.goBack();
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { teacherId, initialValue } = this.state;
    return (
      <Card
        title={
          teacherId ? 'Cập nhật danh mục xếp hạng tốt nghiệp' : 'Thêm danh mục xếp hạng tốt nghiệp'
        }
      >
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item style={{ display: 'none' }} {...formItemLayout} label="id" hasFeedback>
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
              })(<Input />)}
            </Form.Item>
            <Form.Item hasFeedback label="Xếp loại" {...formItemLayout}>
              {getFieldDecorator('label', {
                initialValue: initialValue.label,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tên tiếng anh" hasFeedback>
              {getFieldDecorator('labelInEnglish', {
                initialValue: initialValue.labelInEnglish,
                rules: [
                  {
                    // required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm" hasFeedback>
              {getFieldDecorator('fromGrade', {
                initialValue: initialValue.fromGrade,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Đến điểm" hasFeedback>
              {getFieldDecorator('toGrade', {
                initialValue: initialValue.toGrade,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Từ điểm thang 10" hasFeedback>
              {getFieldDecorator('fromGradeIn10', {
                initialValue: initialValue.fromGradeIn10,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Đến điểm thang 10" hasFeedback>
              {getFieldDecorator('toGradeIn10', {
                initialValue: initialValue.toGradeIn10,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={this.props.history.goBack} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormRatingGraduate {...props} />
  </Page>
));



